body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.auth-container {
  margin-top: 50px;
  padding: 15px;
  width: 300px;
  border: 1px solid #ebe2eb;
  border-radius: 5px;
  box-shadow: 7px 7px 6px 2px rgba(224,224,224,0.7);
}
#home {
  display: flex;
  justify-content: center; }
  #home .buttons {
    display: flex;
    flex-direction: column; }

@keyframes lds-ripple {
  0% {
    top: 96px;
    left: 96px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 18px;
    left: 18px;
    width: 156px;
    height: 156px;
    opacity: 0;
  }
}
@-webkit-keyframes lds-ripple {
  0% {
    top: 96px;
    left: 96px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 18px;
    left: 18px;
    width: 156px;
    height: 156px;
    opacity: 0;
  }
}
.lds-ripple {
  position: relative;
}
.lds-ripple div {
  box-sizing: content-box;
  position: absolute;
  border-width: 4px;
  border-style: solid;
  opacity: 1;
  border-radius: 50%;
  -webkit-animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(1) {
  border-color: #e90c59;
}
.lds-ripple div:nth-child(2) {
  border-color: #fe718d;
  -webkit-animation-delay: -0.5s;
  animation-delay: -0.5s;
}
.lds-ripple {
  width: 24px !important;
  height: 24px !important;
  -webkit-transform: translate(-20px, -20px) scale(0.2) translate(20px, 20px);
  transform: translate(-20px, -20px) scale(0.2) translate(20px, 20px);
}
#login {
  display: flex;
  justify-content: center;
}
#register {
  display: flex;
  justify-content: center;
}
.input-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 20px; }

.options-container {
  display: flex;
  flex-direction: row; }
  @media (max-width: 414px) {
    .options-container {
      flex-direction: column; } }

.additional-options {
  flex-basis: 35%;
  margin-top: 10px;
  margin-right: 20px; }
  @media (max-width: 414px) {
    .additional-options {
      flex-basis: 100%; } }

